"use client";

import React from "react";
import { useRouter, usePathname } from "next/navigation";
import { Briefcase, FileText, UserCircle2 } from "lucide-react";
import Image from "next/image";
import { useSelector } from "react-redux";
import { RootState } from "@/app/redux/store";


export default function DashboardNavbar() {
  const router = useRouter();
  const pathname = usePathname();
  const user = useSelector((state: RootState) => state.user.user);

  const isActive = (path: string) =>
    pathname === path
      ? "text-blue-600 border-b-2 border-blue-600"
      : "text-gray-600 hover:text-blue-600";

  const handleProfileClick = () => {
    if (user?.id) {
      router.push(`/profile/${user.id}`);
    } else {
      router.push("/login");
    }
  };

  return (
    <nav className="w-full bg-white shadow-sm px-6 py-3 flex items-center justify-between">
      <div
        className="flex items-center gap-2 cursor-pointer"
        onClick={() => router.push("/dashboard")}
      >
        <Image src="/jobLogo.png" alt="Logo" width={32} height={32} />
        <span className="text-xl font-semibold text-gray-800">JobPortal</span>
      </div>

      <div className="flex items-center gap-6">
        <button
          onClick={() => router.push("/dashboard")}
          className={`flex items-center gap-1 text-sm font-medium transition ${isActive(
            "/dashboard"
          )}`}
        >
          <Briefcase size={18} />
          Jobs
        </button>

        <button
          onClick={() => router.push("/dashboard/my-jobs")}
          className={`flex items-center gap-1 text-sm font-medium transition ${isActive(
            "/dashboard/my-jobs"
          )}`}
        >
          <FileText size={18} />
          My Jobs
        </button>
      </div>

      <div
        onClick={handleProfileClick}
        className="cursor-pointer p-2 rounded-full hover:bg-gray-100 transition"
      >
        <UserCircle2 size={28} className="text-gray-700" />
      </div>
    </nav>
  );
}
